$btn-width: 315px;
$btn-height: 70px;

$btn-color-text: #000000;
$btn-color-bg: #1DF527;

$btn-font-size: 1.55em;

button{
  transition: all $transition-base ease-out;
  outline: none;
  border: none;
}

button.gamechange{
  position: absolute;
  left: 50%;
  margin-left: -$btn-width / 2;
  top: 50%;
  margin-top: -$btn-height / 2;
  font-size: $btn-font-size;
  font-weight: $font-weight-bold;
  width: $btn-width;
  height: $btn-height;
  color: $btn-color-text;
  background-color: rgba($btn-color-bg, .8);
  &:hover{
    color: darken($btn-color-text, 10%);
    background-color: darken($btn-color-bg, 10%);
    cursor: pointer;
  }      
}

#modal-gamewin button, #modal-gameover button{
  display: block;
  margin: $margin-big auto;
  height: 50px;
  width: 200px;
  background-color: $btn-color-bg;
  color: $btn-color-text;
  font-size: $btn-font-size / 1.5;
  &:hover{
    color: darken($btn-color-text, 10%);
    background-color: darken($btn-color-bg, 5%);
    cursor: pointer;
  }    
}