$interface-width: 100%;
$interface-padding: .5rem;

$canvas-width: 100%;
$canvas-max-width: inherit;
$canvas-height: 85vh;
$canvas-margin: 1rem;

#game-interface{
  color: rgba($white, .8);
  padding: $interface-padding;
  font-size: em(16);
  & span{
    text-transform: uppercase;
  }
  & span#test{
    color: red;
    font-size: em(12);
  }
  &--indicators, &--info{
    float: left;
    width: $interface-width / 2;
  }
  &--indicators{
    text-align: right;
  }
  &-m--info{
  	float: left;
  	width: $interface-width / 2;
  	display: none;
  }
  @include for-size(phone-only) {
  	&--indicators{
  		width: 100%;
  		text-align: right;
  		float: none;
  	}
  	&--info{
  		display: none;
  	}
  	&-m--info{
  		display: block;
  	}
  }
}

#screen{
  margin-top: $canvas-margin;
  height: $canvas-height;
  max-width: $canvas-max-width;
  width: $canvas-width;
  object-fit: contain;
}