*{
  box-sizing: border-box;
}

body{
    background-color: $body-base-color;
    font-family: $font-family-base;
    font-size: em(18);
    margin: 0;
    overflow: hidden;
}

img{
    max-width: 100%;
}

.container{
  width: 100%;
  padding-left: $padding-base;
  padding-right: $padding-base;
  @include push--auto();
  @include for-size(desktop-up){
    max-width: $desktop-up;
  }
}

#starfield{
  width: 100%;
  height: 100%;
  position: absolute;
  left: 0px;
  top: 0px;
  z-index: -1;
  overflow: hidden;
}
  
span.gamechange{
  color: #F6FF0B;
  font-size: 1em;
        
}

#pause{
  display: none;
}