#modal-gamewin, #modal-gameover{
  width: 100%;
  height: 100%;
  position: absolute;
  background-color: #111;
  top: 0;
  left: 0;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  display: none;
  h2{
    font-size: 2em;
    margin: 0;
  }
  @include for-size(phone-only) {
    text-align: center;
    font-size: em(14);
  }       
}
  
#modal-gamewin h2{
  color: green;
}
#modal-gameover h2{
  color: red;
}
  
#modal-gamewin p, #modal-gameover p{
  color: #eee;
}
  